<template>
  <el-drawer
    v-model="initData.visible"
    class="custom-drawer"
    size="800px"
    title="话术模板"
    :before-close="handleClose"
  >
    <el-form>
      <el-form-item label="模板名称">{{ speechInfo?.title }}</el-form-item>
      <el-form-item label="创建人">{{ speechInfo?.createByName }}</el-form-item>
      <el-form-item label="创建时间">{{
        speechInfo?.createdTime
      }}</el-form-item>
      <el-form-item class="speech-content">
        <div class="content" v-html="speechInfo?.content"></div>
      </el-form-item>
      <el-form-item class="examine-list">
        <el-table :data="speechInfo?.list" height="200" style="width: 100%">
          <el-table-column
            align="center"
            width="150"
            prop="operateName"
            label="审核人"
          />
          <el-table-column
            align="center"
            width="200"
            prop="operateTime"
            label="审核时间"
          />
          <el-table-column
            align="center"
            width="150"
            prop="operateLabel"
            label="审核内容"
          />
          <el-table-column align="center" prop="remark" label="备注">
            <template #default="{ row }">
              <el-popover
                placement="top-start"
                :width="200"
                trigger="hover"
                :content="row.remark"
              >
                <template #reference>
                  <span>{{ row.remark }}</span>
                </template>
              </el-popover>
            </template>
          </el-table-column>
        </el-table>
      </el-form-item>
    </el-form>
    <template #footer>
      <div class="dialog-footer">
        <el-button @click="handleClose">关闭</el-button>
      </div>
    </template>
  </el-drawer>
</template>

<script lang="ts" setup>
import { ref, shallowReactive } from "vue";
const initData = shallowReactive({
  visible: false,
});

const speechInfo = ref({});

function handleClose() {
  initData.visible = false;
}

function trigger(visible: boolean, info: Record<string, any>) {
  initData.visible = visible;
  speechInfo.value = info;
}
defineExpose({
  trigger,
});
</script>

<style lang="scss" scoped>
.el-form {
  display: flex;
  flex-wrap: wrap;
  .el-form-item {
    width: 50%;
    margin-right: 0;
    margin-bottom: 15px;
  }
  .examine-list {
    width: 100%;
  }
  .speech-content {
    width: 100%;
    .content {
      width: 100%;
      min-height: 200px;
      max-height: 500px;
      padding: 10px;
      border: 1px solid #ccc;
      overflow-y: auto;
    }
  }
}
</style>
